.statisticContainer {
    width: 100%;
    background-color: #FFFFFF;
    border-radius: 16px;
    padding: 25px;
    display: flex;
    flex-direction: column;
    gap: 35px;
}


.stats {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.dataBtn {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
}

.buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.funcButton {
    border-radius: 100px;
    padding: 20px 74px;
    cursor: pointer;
    border: none;
}

.dataBtnName {
    color: #021E2F;
    font-size: 18px;
    line-height: 21.6px;
}